<template>
            <div class="page" @click="closePop()" v-show="show">
                <div class="qrViewF">
                    <img src="http://uniondrug.oss-cn-hangzhou.aliyuncs.com/tm-new-yaodianbao/%E8%8D%AF%E5%BA%97%E5%AE%9D3.5.3%E5%9B%BE%E7%89%87/yaodianbao_activity_qr.png">
                </div>
            </div>
</template>

<script>
    export default {
        name: "index",
        props: {
            show: Boolean,
        },
        data() {
            return {
                t1:'',
            }
        },
        watch: {
            show(newV, oldV) {
                if (newV) {
                    //10s没有成功就自动关闭
                    this.t1=setTimeout(() => {
                        this.show = false;
                    }, 10000);
                }else {
                    clearTimeout(this.t1)

                }

            },

        },
        methods:{
            closePop(){
                this.show = false;
            }
        }

    }
</script>

<style scoped>
    .qrViewF{
        width: 100vw;
        height: 100vh;
        background: rgba(0,0,0,0.9);
        display: flex;
        align-items: center;
        justify-content: center;
        position: fixed;
        top: 0;
        left: 0;
        z-index: 100;
    }
    .qrViewF img{
        width: 100vw;
        height: 100vw;
    }

