//http 相应 拦截器
axios.interceptors.response.use(response => {
        if(sessionStorage.getItem('lunxun') == '1'){

        }else {
            Vue.$loading.hide();
        }
        if (response.data.errno === '0') {
            return response.data;
        } else if (response.data.errno === '401' || response.data.errno === '403' || response.data.errno === '820403') {
            // Toast('请重新登录');
            return response.data;
        } else {
            if (/[a-zA-Z]/.test(response.data.error)) {
                Toast('网络问题，请稍后再试');
            } else {
                Toast(response.data.error);
            }
            return response.data;
        }

    },
    error => {
        return Promise.reject(error)
    }
)

/**
 * 封装get方法
 * @param url
 * @param data
 * @returns {Promise}
 */

export function get(url, params) {
    axios.defaults.baseURL = getLocalbaseURL();
    return new Promise((resolve, reject) => {
        axios.get(url, {
            params: params,
            headers: {'Authorization': 'Bearer ' + sessionStorage.getItem('token')}
        })
            .then(response => {
                if (response.errno == '0') {
                    resolve(response.data);  //成功回调
                } else {
                    reject(response.error);  //接口报错回调
                }

            })
            .catch(err => {
                reject(err)
            })
    })
}

/**
 * 封装post请求
 * @param url
 * @param parameter
 * @returns {Promise}
 */

export function post(url, parameter, type) {
    if (!type) {
        Vue.$loading.show();
    }


    let token;
    if(sessionStorage.getItem('token')){
        token = sessionStorage.getItem('token')
    }else if(uniondrug.getToken()){
        console.log(uniondrug.getToken())
        token = uniondrug.getToken();
    }
    axios.defaults.headers.post['Authorization'] = 'Bearer ' + token;
    axios.defaults.baseURL = getLocalbaseURL();
    return new Promise((resolve, reject) => {
        axios.post(url, parameter)
            .then(response => {
                if (response.errno == '0') {
                    resolve(response.data);  //成功回调
                }else if(response.errno == 401 || response.errno == 403){
                    let env = sessionStorage.getItem('token');
                    if (env) {

                    } else {
                        window.location.replace(auth + '/authority?redirect_uri=' + redirect_uri)
                    }
                }else {
                    reject(response.error);  //接口报错回调
                }

            })
            .catch(err => {
                console.log('111', err);
                // location.href = '/yaodianbao/404';
                reject(err)
            })
    })
}

