//获取txt中的文本
export function getText(url, files) {
    let ajx = new XMLHttpRequest();
    ajx.open("get", url, true);
    return new Promise((resolve, reject) => {
        ajx.onreadystatechange = () => {
            if (ajx.readyState != 4) {
                return;
            }
            if (ajx.status >= 200 && ajx.status < 300) {
                resolve(ajx.responseText)
            }
        };
        ajx.send();
    })
}

/**
 * 封装upload上传文件请求
 * @param url
 * @param files
 * @returns {Promise}
 */
export function upload(url, files) {
    console.log('files', files);
    Vue.$loading.show();
    let params = new FormData(); //创建form对象
    params.append("file", files); //通过append向form对象添加数据//第一个参数字符串可以填任意命名，第二个根据对象属性来找到file
    let config = {
        headers: {'Content-Type': 'multipart/form-data'}
    };  //添加请求头
    return new Promise((resolve, reject) => {
        axios.post(getLocalbaseURL() + url, params, config)
            .then(res => {
                resolve(res.data)
            }, err => {
                reject(err)
            })
    })

}

