// index.js
import LoadingComponent from './index.vue'

let $vm;

export default {
    install(Vue, options) {
        if (!$vm) {
            const LoadingPlugin = Vue.extend(LoadingComponent);

            $vm = new LoadingPlugin({
                el: document.createElement('div')
            });

            document.body.appendChild($vm.$el);
        }
        // console.log('$vm',$vm);

        $vm.show = false;

        let bigImg = {
            show() {
                $vm.show = true;
            },
            hide() {
                $vm.show = false;
            }
        };

        if (!Vue.$showBigImg) {
            Vue.$showBigImg = bigImg;
        }

        Vue.prototype.$showBigImg = Vue.$showBigImg;

        // Vue.mixin({
        //     created() {
        //         this.$loading = Vue.$loading;
        //     }
        // })
    }
}
