import Vue from 'vue'
import App from './App.vue'
import setRem from './libs/rem.js';
import router from './router'
import store from './store'
import Vant from 'vant';
import VConsole from 'vconsole'
import api from '@/libs/apiServe'
import moment from 'moment';//时间计算器
import 'vant/lib/index.css';
import Cookies from 'js-cookie'
import bs from './lz-jsbridge'
import loading from './components/loding/loading'
import showBigImg from './components/showBigImage/index'
import './libs/debounce' //点击事件的防抖和节流 v-debounce="[() => { submit() }, 500]"
import monitor from './libs/monitor'
import Bridge from './libs/bridge.js'
// import bridgeSystem from "./libs/bridgeSystem";
import publicFunction from "./libs/publicFunction";
import MtaH5 from 'mta-h5-analysis';
import popRefuse from "./components/popRefuse/popRefuse";
import VueBarcode from '@xkeshi/vue-barcode' // 条码生成器
import './app.css'
import '../src/libs/autotrack'
import '../public/js/mobile-detect.min.js'
import trackFn from "./libs/trackFn";
import {parms} from './libs/buriedPoint'
import 'videojs-contrib-hls'
require('video.js/dist/video-js.css')
require('vue-video-player/src/custom-theme.css')
new VConsole();
Cookies.set('uuid', '123654')
/**配置rem 后面宽度使用px自动转换rem,屏宽750*/
setRem();
/**I框架组建因为懒所以全部引用*/
Vue.use(Vant);
/**jsbridge与APP交互*/
Vue.use(bs);
console.log('111');

/**自定义的加载中loading*/
Vue.use(loading);//自定义的加载中loading


/**显示预览图*/
Vue.use(showBigImg);//自定义金币掉落


/**自定义取消订单弹窗*/
Vue.use(popRefuse);
Vue.component('barcode', VueBarcode);


/**全局cookies*/
Vue.prototype.$cookie = Cookies;


/**全局时间计算器  http://momentjs.cn/
 * 使用方法console.log((this.$moment(new Date()).subtract(2, 'days').format('YYYY-MM-DD')));*/
Vue.prototype.$moment = moment;
moment.locale('zh-cn');


/**全局api*/
Vue.prototype.$api = api;

/**处理安卓手机键盘弹出压缩页面bug*/
let hrt = document.documentElement.clientHeight; //获取当前可视区域的高度存到hrt变量
window.onload = function () { //在页面整体加载完毕时
    document.getElementById('app').style.minHeight = hrt + 'px'//把获取到的高度赋值给根div
};
/**数据上报组件*/
let handler = monitor.bind(function (e) { //数据上报组件
    e = e || window.event;
    monitor._trackEvent(e, 'aaa', MtaH5);
}, this);
document.addEventListener('click', handler, true);
/**兼容ios软键盘*/
document.body.addEventListener('focusout', () => { //软键盘关闭事件
    document.body && (document.body.scrollTop = window.document.body.scrollTop);
});

Vue.prototype.$bridge = Bridge;//与IOS和安卓数据交互组建,
Vue.prototype.$trackFn = trackFn;//神策数据埋点
Vue.prototype.$parms = parms;//神策数据埋点
Vue.prototype.$publicF = publicFunction;//常用方法

/**w
 /https://www.npmjs.com/package/qrcode二维码控件

 Vue.config.productionTip = false;
 */

// 初始化
MtaH5.init({
    "cid": '500717289', //如果开启自定义事件，此项目为必填，否则不填
});
Vue.prototype.$MtaH5 = MtaH5;//与IOS和安卓数据交互组建,
//动态修改页面title
router.beforeEach((to, from, next) => {
    if (to.meta.title) {
        document.title = to.meta.title;
    }
    if (to.query.token) {
        sessionStorage.setItem('token', to.query.token);
    }
    if (to.query._env) {
        sessionStorage.setItem('_env', to.query._env);
    }
    if (typeof to.query._t !== "undefined") {
        next();
    } else {
        to.query._t = new Date().getTime().toString();
        next(to);
    }

});

new Vue({
    router,
    store,
    render: h => h(App)
}).$mount('#app');
