import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router);
/**keepAlive 判断页面是否需要缓存,
 * noVerify 控制当前页面是否不需要验证用户信息
 */
export default new Router({
    scrollBehavior(to, from, savedPosition) {
        return {
            x: 0,
            y: 0
        }
    },
    base: process.env.BASE_URL,
    routes: [
        {
            path: path('openClass/home'),
            name: 'home',
            component: () => import('./views/openClass/home'),
            meta: {
                title: '药联课堂',
            },
        },

        {
            path: path('openClass/push'),
            name: 'push',
            component: () => import('./views/openClass/push'),
            meta: {
                title: '申请开课',
            },
        },
        {
            path: path('openClass/detail'),
            name: 'detail',
            component: () => import('./views/openClass/detail'),
            meta: {
                title: '直播详情',
            },
        },
        {
            path: path('openClass/inofUp'),
            name: 'inofUp',
            component: () => import('./views/openClass/inofUp'),
            meta: {
                title: '修改信息',
            },
        },{
            path: path('openClass/link'),
            name: 'link',
            component: () => import('./views/openClass/link'),
            meta: {
                title: '',
            },
        },{
            path: path('openClass/qianList'),
            name: 'qianList',
            component: () => import('./views/openClass/qianList'),
            meta: {
                title: '获取课程收益',
            },
        },{
            path: path('openClass/read'),
            name: 'read',
            component: () => import('./views/openClass/read'),
            meta: {
                title: '劳务协议',
            },
        },{
            path: path('openClass/idPage'),
            name: 'idPage',
            component: () => import('./views/openClass/idPage'),
            meta: {
                title: '上传身份证',
            },
        },{
            path: path('openClass/autograph'),
            name: 'autograph',
            component: () => import('./views/openClass/autograph'),
            meta: {
                title: '电子签名',
            },
        },{
            path: path('openClass/card'),
            name: 'card',
            component: () => import('./views/openClass/card'),
            meta: {
                title: '添加银行卡',
            },
        },
        {
            path: path('openClass/radio'),
            name: 'radio',
            component: () => import('./views/openClass/radio'),
            meta: {
                title: '回放',
            },
        }
    ]
})

// 拼装
export function path(url) {//拼接上二级域名
    return '/yaodianbao/' + url;
}
