/**
 * let NewPage = '_empty' + '?time=' + new Date().getTime()/500;
 this.$router.push(NewPage);
 this.$router.go(-1);

 刷新页面简便写法
 */

import moment from 'moment';//时间计算器
export default {
    getTxt(url) {
        let ajx = new XMLHttpRequest();
        ajx.open("get", url, true);
        return new Promise((resolve, reject) => {
            ajx.onreadystatechange = () => {
                if (ajx.readyState != 4) {
                    return;
                }
                if (ajx.status >= 200 && ajx.status < 300) {
                    resolve(ajx.responseText)
                }
            };
            ajx.send();
        })
    },

    //验证手机号合法性
    isPoneAvailable(mobile) {
        return /^1[3-9][0-9]\d{4,8}$/.test(mobile);

    },
    //手机号码中间加星号
    concealPone(phone) {
        return phone.substr(0, 3) + '****' + phone.substr(7);
    },
    //验证身份证合法性
    isCodeAvailable(code) {
        return /(^\d{15}$)|(^\d{18}$)|(^\d{17}(\d|X|x)$)/.test(code);
    },
    //身份证码中间加星号
    concealCode(code) {
        return code.substr(0, 3) + '********' + code.substr(11);
    },

    format(time,splice,type) {
//shijianchuo是整数，否则要parseInt转换
        let y = time.getFullYear();
        let m = time.getMonth() + 1;
        let d = time.getDate();
        let h = time.getHours();
        let mm = time.getMinutes();
        let s = time.getSeconds();
        if(type=='day'){
            return y + splice + this.add0(m) + splice + this.add0(d)
        }else {
            return y + splice + this.add0(m) + splice + this.add0(d) + " " + this.add0(h) + ":" + this.add0(mm) + ":" + this.add0(s);
        }


    },
    add0(m) {
        return m < 10 ? "0" + m : m;
    },
    //数组转字符串
    listToString(list) {
        let data = '';
        list.forEach((item, index) => {
            data += item.url;
            if (list.length !== index + 1) {
                data += ','
            }
        });
        return data;
    },

    //处理深拷贝问题
    deepClone(obj) {
        let _obj = JSON.stringify(obj),
            objClone = JSON.parse(_obj);
        return objClone
    },
    //是否在微信内打开
    isWeixin() {
        let wx = navigator.userAgent.toLowerCase();
        console.log(wx.match(/MicroMessenger/i) == 'micromessenger');
        if (wx.match(/MicroMessenger/i) == 'micromessenger') {
            return true
        } else {
            return false
        }
    },
    //时分秒
    formatSeconds(value) {
        var secondTime = parseInt(value);// 秒
        var minuteTime = 0;// 分
        var hourTime = 0;// 小时
        var dayTime = 0;// 天
        if (secondTime >= 60) {//如果秒数大于60，将秒数转换成整数
            //获取分钟，除以60取整数，得到整数分钟
            minuteTime = parseInt(secondTime / 60);
            //获取秒数，秒数取佘，得到整数秒数
            secondTime = parseInt(secondTime % 60);
            //如果分钟大于60，将分钟转换成小时
            if (minuteTime >= 60) {
                //获取小时，获取分钟除以60，得到整数小时
                hourTime = parseInt(minuteTime / 60);
                //获取小时后取佘的分，获取分钟除以60取佘的分
                minuteTime = parseInt(minuteTime % 60);
                //如果小时大于24，将小时转换成天
            }
            if (hourTime >= 24) {
                dayTime = parseInt(hourTime / 24);
                hourTime = parseInt(hourTime % 24);
            }

        }
        var result = "" + parseInt(secondTime) + "秒";

        if (minuteTime > 0) {
            result = "" + parseInt(minuteTime) + "分" + result;
        }
        if (hourTime > 0) {
            result = "" + parseInt(hourTime) + "时" + result;
        }
        if (dayTime > 0) {
            result = "" + parseInt(dayTime) + "天" + result;
        }
        return result;
    },
    //接口对象拼接字符串
    spliceUrl(data) {
        let path = data.path;
        let query = data.query;
        let url = path + '?';
        let queryString = '';
        for (let key  in query) {
            queryString += key + '=' + query[key] + '&'
        }
        console.log(url + queryString);
        return url + queryString

    },
    number_format(number, decimals, dec_point, thousands_sep) {
        /**
         * 参数说明：
         * number：要格式化的数字
         * decimals：保留几位小数
         * dec_point：小数点符号
         * thousands_sep：千分位符号
         */
        // number.matches("-?[0-9]+.*[0-9]*");	//可以验证正数,负数,小数,0
        number = (number + '').replace(/[^0-9+-Ee.]/g, '');
        var n = !isFinite(+number) ? 0 : +number,
            prec = !isFinite(+decimals) ? 2 : Math.abs(decimals),
            sep = (typeof thousands_sep === 'undefined') ? ',' : thousands_sep,
            dec = (typeof dec_point === 'undefined') ? '.' : dec_point,
            s = '',
            toFixedFix = function (n, prec) {
                var k = Math.pow(10, prec);
                return '' + Math.ceil(n * k) / k;
            };

        s = (prec ? toFixedFix(n, prec) : '' + Math.round(n)).split('.');
        var re = /(-?\d+)(\d{3})/;
        while (re.test(s[0])) {
            s[0] = s[0].replace(re, "$1" + sep + "$2");
        }

        if ((s[1] || '').length < prec) {
            s[1] = s[1] || '';
            s[1] += new Array(prec - s[1].length + 1).join('0');
        }
        return s.join(dec);
    },
    //获取url参数
    getQueryString(name,url) {
        var reg = new RegExp("(^|&)" + name + "=([^&]*)(&|$)", "i");
        let urlData=url.split('?');
        var r = urlData[1].match(reg);
        if (r != null) return decodeURI(r[2]);
        return null;
    },
    // 获取时间范围
    getTimeRange(index, data = {}) {
        switch (index) {
            case 0:
                data.startDate = moment(new Date()).format('YYYY-MM-DD')
                data.endDate = moment(new Date()).format('YYYY-MM-DD')
                break;
            case 1:
                data.startDate = moment().week(moment().week()).startOf('week').format('YYYY-MM-DD')
                data.endDate = moment().week(moment().week()).endOf('week').format('YYYY-MM-DD')
                break;
            case 2:
                data.startDate = moment().week(moment().week()).startOf('month').format('YYYY-MM-DD')
                data.endDate = moment().week(moment().week()).endOf('month').format('YYYY-MM-DD')
                break;
            default:
                break;
        }
        return data
    }
}

