<template>
            <div class="page" @click.stop="" v-show="show">
                <div class="loadingView">
                    <van-loading size="24px" vertical>加载中...</van-loading>
                </div>
            </div>
</template>

<script>
    export default {
        name: "loading",
        props: {
            show: Boolean,
        },
        data() {
            return {
                t1:'',
            }
        },
        watch: {
            show(newV, oldV) {
                if (newV) {
                    //10s没有成功就自动关闭
                    this.t1=setTimeout(() => {
                        this.show = false;
                    }, 10000);
                }else {
                    clearTimeout(this.t1)

                }

            },

        },

    }
</script>

<style scoped>
    .page{
        position: fixed;
        width: 100vw;
        height: 100vh;
        display: flex;
        align-items: center;
        justify-content: center;
        top: 0;
        left: 0;
        z-index: 999;
    }
    .loadingView{
        display: flex;
        align-items: center;
        justify-content: center;
        width: 30vw;
        height: 30vw;
        background: rgba(0,0,0,0.7);
        border-radius: 6px;
    }
